*{
    box-sizing: border-box;
  }
  .normal-display {
    display: block;
  }
  .fugitive-display {
    display:none;
  }
  :root {
    --font-color: rgb(36, 34, 68);
    --font-on-dark: white;
    --scale: scale(1.4);
    --scale2: scale(0.5)
  }
  .wedding-card {
    margin: 0 !important;
    padding: 0 !important;
  }
  .centerBox{
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    opacity: 1;
  }
  .bgimg {
    z-index: -1;
    left: 0; 
    right: 0;
    margin-left: auto; 
    margin-right: auto;
    -webkit-transition: all 0.5s;
    -moz-transition: all 0.5s;
    -o-transition: all 0.5s;
    transition: all 0.5s;
  }
  .active {
    filter: blur(10px) opacity(20%) contrast(100%);
  }
  .bluestars {
    filter: invert(37%) sepia(8%) saturate(3405%) hue-rotate(174deg) brightness(89%) contrast(97%);
  }
  .blueborder {
    border: #315d96;
  }
  .dresscode {
    max-width : 15px;
  }
  .categoryWrapper{
    overflow: hidden;
    position: absolute;
    width: 100%;
    height:100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .categoryWrapper:after{
    top:0;
    left: 0;
    right:0;
    bottom: 0;
  }
  .categoryWrapper:hover:after{
    opacity: 0.4;
  }

  /* WRITING */
  .categoryWrapper h5{
    width: 100%;
    text-align: center;
    font-size: 25px;
    -webkit-transition: all 1s;
    -moz-transition: all 1s;
    -o-transition: all 1s;
    transition: all 1s;
    color: var(--font-on-dark);
    background:rgba(0, 0, 0, 0.356);
  }
  .categoryWrapper:hover h5{
    transform: translateY(-20px);
    color: var(--font-color);
    background: none;
  }

  /* SUR MAC */
  .raclette-content {
    position: absolute;
    font-size: 12px;
    width: 80%;
    -webkit-transition: all 1s;
    -moz-transition: all 1s;
    -o-transition: all 1s;
    transition: all 1s;
    padding: 10px;
  }
  .raclette-hide {
    opacity: 0;
  }
  .raclette-show {
    opacity: 1;
  }
  .date-title {
    font-size: 15px;
    -webkit-transition: all 2s;
    -moz-transition: all 2s;
    -o-transition: all 2s;
    transition: all 2s;
  }
  .normal-text {
    font-size: 10px;
    -webkit-transition: all 1s;
    -moz-transition: all 1s;
    -o-transition: all 1s;
    transition: all 1s;
  }
  .small-text {
    font-size: 8px;
    -webkit-transition: all 1s;
    -moz-transition: all 1s;
    -o-transition: all 1s;
    transition: all 1s;
  }
  .head-text {
    font-size: 14px;
    -webkit-transition: all 1s;
    -moz-transition: all 1s;
    -o-transition: all 1s;
    transition: all 1s;
  }
  .bold {
    font-weight: bold;
  }
  .location-text {
    color: orange;
    font-size: 13px;
    -webkit-transition: all 1s;
    -moz-transition: all 1s;
    -o-transition: all 1s;
    transition: all 1s;
  }
  .rmoveup {
    margin-bottom: 400px;
  }
  .rmovedown {
    margin-top: 300px !important;
  }
  /* FIN MAC TEST */


  /* BUTTON */

  .categoryWrapper button{
    -webkit-transition: all 1s;
    -moz-transition: all 1s;
    -o-transition: all 1s;
    transition: all 1s;
    position: absolute;
    transform: translatey(60px);
    -webkit-appearance: none;
    border: none;
    background: none;
    color: var(--font-color);
    width: 250px;
    height:50px;
    font-size: 20px;
    padding: 0;
    margin: 0;
    outline: none;
    z-index: 10;
    color: var(--font-on-dark)
  }
  
  .categoryWrapper button span{
    display: block;
    line-height: 50px;
    height: 50px;
    cursor: pointer;
  }
  
  .categoryWrapper button > span:after{
    content:'';
    position: absolute;
    top:0;
    left: 50%;
    width: 20px;
    height: 0;
    border: 1px solid var(--font-color);
    border-left: none;
    border-bottom: none;
    transition: height 0.15s ease-out, width 0.15s ease-out 0.15s;
  }
  
  .categoryWrapper:hover button > span:after{
    width: calc(50% - 1px);
    height: calc(100% - 2px);
    transition: width 0.15s ease-out, height 0.15s ease-out 0.15s;
  }
  
  .categoryWrapper button > span:before{
    content:'';
    position: absolute;
    top:0;
    right: 50%;
    width: 20px;
    height: 0;
    
    border: 1px solid var(--font-color);
    border-right: none;
    border-bottom: none;
    
        transition: height 0.15s ease-out, width 0.15s ease-out 0.15s;
  }
  
  .categoryWrapper:hover button > span:before{
    width: calc(50% - 1px);
    height: calc(100% - 2px);
    transition: width 0.15s ease-out, height 0.15s ease-out 0.15s;
  }
  
  .categoryWrapper button > span > span:before{
    content:'';
    position: absolute;
    bottom:0;
    right: 0%;
    width: 1px;
    height: 1px;
    opacity: 0;
    
  }
  
  .categoryWrapper:hover button > span > span:before{
    opacity: 1;
    border-bottom: 1px solid var(--font-color);
    width: calc(50%);
    height: 1px;
    transition: opacity 0s ease-out 0.29s, width 0.15s ease-out 0.3s;
  }
  
  .categoryWrapper button > span > span:after{
    content:'';
    position: absolute;
    bottom:0;
    left: 0%;
    width: 1px;
    height: 1px;
    opacity: 0;
    
  }
  
  .categoryWrapper:hover button > span > span:after{
    opacity: 1;
    border-bottom: 1px solid var(--font-color);
    width: calc(50%);
    height: 1px;
    transition: opacity 0s ease-out 0.29s, width 0.15s ease-out 0.3s;
  }
  
  .categoryWrapper button > span > span > span{
    transition: color 0.15s ease-out 0.3s;
    color: transparent;
  }
  
  .categoryWrapper:hover button > span > span > span{
    color:var(--font-color);
  }
  
  .categoryWrapper button > span > span > span:after{
    position: absolute;
    top:0;left:0;right:0;bottom:0;
    color:#1f2e4d;
    content: attr(data-attr-span);
    width: 0%;
    height: 100%;
    background:var(--font-color);
    white-space: nowrap;
    text-align: center;
    margin: auto;
    overflow: hidden;
    display: flex;
    justify-content: center;
    transition: width 0.2s;
  }
  
  .categoryWrapper button:hover > span > span > span:after{
    width: 100%;
    color:var(--font-on-dark);
  }

  /* HOTELS */
  
  .info-hotel {
    height: 350px;
    text-align: center;
    display:flex;
    padding-top: 20px;
    padding-bottom: 20px;
  }
  .show {
    display: block !important; 
    -webkit-transform: translateY(-350px);
    -moz-transform: translateY(-350px);
    -o-transform: translateY(-350px);
    -ms-transform: translateY(-350px);
    transform: translateY(-350px);
  }
  .fivestars, .fourstars, .threestars {
    -webkit-transition: all 1s;
    -moz-transition: all 1s;
    -o-transition: all 1s;
    transition: all 1s;
    margin-top: 350px;
    margin-left: 25%;
    margin-right: 25%;
    border: solid 1px rgb(187, 187, 187);
    background: rgba(255, 255, 255);
    border-radius: 5px 30px 5px;
    padding: 20px;
    width: 50%;
    position: absolute;
  }
  .hotels-row:hover {
    cursor: pointer;
  }
  .categoryWrapper h4{
    width: 100%;
    padding: 50px;
    height: 150px;
    text-align: center;
    -webkit-transition: all 1s;
    -moz-transition: all 1s;
    -o-transition: all 1s;
    transition: all 1s;
    color: var(--font-on-dark);
  }
  .categoryWrapper:hover h4{
    transform: translateY(-20px);
    color: var(--font-color);
    background: none;
  }
  .hotels-col {
    margin: 0 auto !important;
    padding: 0 auto !important;
  }
  /* DESKTOP  */
@media (min-width: 1025px) {
  .hotels-row.col-sm {
    height: 100px !important;
  }
  .hotels-row .categoryWrapper {
