/* NAVIGATION */

.nav-main {
  margin-left: 20px;
  margin-right: 30px;
}

/* Link */
a {
  font-size: 12px;
  color: white;
  display: block;
  text-decoration: none;
  position: relative;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}
a:hover {
  text-decoration: none;
  color:white;
}
.link {
  cursor: pointer;
}

/* Toggling part */
.supertoggle {
  position: fixed;
  z-index: 3;
  border: none;
  background: transparent;
  color: white;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}
.supertoggle.notvisible {
  display: none !important;
}
.asupertoggle {
  font-size: 35px;
  font-weight: lighter;
  text-decoration: none;
  color: #272727;
  cursor: pointer;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
  top: 35px;
  left: 40px;
}
.asupertoggle:hover {
  color: #bababa;
}
.toggle {
  position: fixed;
  top: 40px;
  left: 40px;
  z-index: 2;
  border: none;
  background: transparent;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}
.atoggle {
  font-size: 30px;
  text-decoration: none;
  color: #242424;
  cursor: pointer;
  
}
.atoggle:hover {
  color: #242424;
}
.supermenu {
  margin: 0;
  padding: 0;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgb(238, 238, 238);
  z-index: 3;
  transition: 0s;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
  opacity: 0;
  visibility: hidden;
}
