} */
.superul {
  width: 100%;
  margin: 0;
  padding: 0;
  position: absolute;
  text-align: center;
  z-index: 2;
}
.superli {
  list-style: none;
}
.fa-bars, .fa-times {
  color: #315d96;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}
.fa-bars:hover, .fa-times:hover {
  color: #083064;
}
.superli a {
  font-size: 17px;
  padding: 10px;
  display: inline-block;
  text-decoration: none;
  text-transform: uppercase;
  color: #262626;
  -webkit-transition: all 1s;
  -moz-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s;
}
.superli a:hover {
  background: transparent;
}
.supermenu.superactive {
  left : 0;
  overflow: auto;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
  z-index: 2;
  opacity: 1;
  visibility: visible;
}
hr.sidemenu {
  border: solid 0.1em rgb(126, 126, 126);
  border-width: 1px 0 0;
  z-index: 5;
}

/* MOBILE  */
@media (max-width: 600px) {
  .mynavbar {
    position: absolute;
    z-index: 2;
    background-color: transparent !important;
    width: 100%;
  }
  .superul {
    width: 100% !important;
  }
  .superli a {
    font-size: 15px !important;
  }
  .hashtag {
    display: none;
  }
  .logo-toggler {
    margin: 0 auto;
    text-align: center;
    width: 33%;
    background: transparent;
  }
  .logo-toggler2 {
    display: none;
  }
  .logo-couple {
    margin: 0 auto;
    text-align: center;
    width: 33%;
  }
  .insta {
    text-align: center;
    width: 33%;
  }
  .insta-logo {
    width: 20px;
  }
  .insta-large-screen {
    display: none;
  }
  .not-visible-for-desktop {
    font-size: 9px;
  }
  .fa-bars {
    -webkit-transition: all 0.5s;
    -moz-transition: all 0.5s;
    -o-transition: all 0.5s;
    transition: all 0.5s;
    filter: drop-shadow(2px 3px 9px #ffffff);
    background: transparent;
  }
  .atoggle, .toggle {
    background: transparent !important;
  }
}
/* DESKTOP  */
@media (min-width: 1025px) {
    /* NAVBAR */
    .collapse {
      margin-left: 50px !important;
      margin: 0 auto;
    }
    .toggledesktop {
      position: fixed;
      top: 40px;
      left: 40px;
      z-index: 2;
      border: none;
      background: transparent !important;
      -webkit-transition: all 0.5s;
      -moz-transition: all 0.5s;
      -o-transition: all 0.5s;
      transition: all 0.5s;
    }
    .fa-bars {
      -webkit-transition: all 0.5s;
      -moz-transition: all 0.5s;
      -o-transition: all 0.5s;
      transition: all 0.5s;
      filter: drop-shadow(2px 3px 10px #ffffff);
      color: #315d96;
    }
    .fa-bars:hover {
      color: #083064;
    }
    .mynavbar {
      letter-spacing: 2px;
      background: rgba(0, 0, 0, 0) !important;
      color:var(--font-color-dbg);
      height: 120px;
      width: 100%;
      position:absolute;
      padding-bottom: 20px;
      padding-top: 20px;
      z-index: 120;
    }
    .logo-toggler {
      display: none !important;
    }
    .insta {
      text-align: center;
    }
    .hashtag {
      font-size: 8px;
    }
    .not-visible-for-desktop {
      display: none;
    }
    .logo-couple {
      display: none;
    }
    .insta-logo {
      display: none;
    }
    .insta-large-screen {
      width: 35px;
    }
}
/* IPAD */
@media (max-width: 1024px) and (min-width: 600px) {
  .mynavbar {
    position: absolute;
    z-index: 2;
    background-color: transparent !important;
    width: 100%;
  }
  .logo-toggler {
    text-align: center;
    width: 20%;
  }
  .logo-toggler2 {
    display: none;
  }
  .logo-couple {
    text-align: center;
    margin: 0 auto;
    width: 60%;
  }
  .collapse {
    display: none !important;
  }
  .insta {
    text-align: center;
    width: 20%;
  }
  .insta-logo {
    width: 25px;
  }
  .insta-large-screen {
    display: none;
  }
  .hashtag {
    display: none;
  }
  .not-visible-for-desktop {
    font-size: auto;
  }
  .superul {
    margin-top: 200px;
  }
  .fa-bars {
    -webkit-transition: all 0.5s;
    -moz-transition: all 0.5s;
    -o-transition: all 0.5s;
    transition: all 0.5s;
    filter: drop-shadow(2px 3px 10px #ffffff);
  }
}
/* MACBOOK SCREEN  */
@media (max-height: 780px) and (min-width: 1025px) {
  .main-logo {
    width: 95px;
  }
}
/* OLD PHONES */
@media (max-width: 321px) {
  .main-logo {
    width: 95px;
  }
  .logo-couple {
    justify-content: center;
    margin: 0 auto;
    text-align: center;
    display: flex;
  }
  .insta-logo {
    width: 25px;
  }
  .insta-large-screen {
    display: none;
  }
}
@media (min-width: 1025px){
  .navbar-expand-custom {
      flex-direction: row;
      flex-wrap: nowrap;
      justify-content: flex-start;
  }
  .navbar-expand-custom .navbar-nav {
      flex-direction: row;
  }
  .navbar-expand-custom .dropdown-menu {
      position: absolute;
  }
  .navbar-expand-custom .nav-link {
      padding-right: .5rem;
      padding-left: .5rem;
  }
  .navbar-expand-custom > .container {
      flex-wrap: nowrap;
  }
  .navbar-expand-custom .navbar-collapse {
      display: flex!important;
      flex-basis: auto;
  }
  .navbar-expand-custom .navbar-toggler {
      display: none;
  }
}