
/* MOBILE  */
@media (max-width: 600px) {
    :root {
        --height: 710px;
    }
    .lg-screen-only {
        display: none;
    }
    .text-oncarousel {
        font-size: 20px;
        margin-top: 170px !important;
        z-index: 3;
        color: white;
        text-align: center;
        width: 100%;
        position: absolute;
      }
    .div-for-parallax {
        height: var(--height);
        justify-content: center;
        overflow: hidden;
        margin: auto !important;
        max-width: 100%;
        z-index: -1;
    }
    .img-prop {
        height: var(--height);
        display: flex;
        z-index: -1;
        transform: translateX(-200px);
        margin-left: auto;
        margin-right: auto;
    }
    .space-header-timer {
        margin-top: 240px;
    }
    .space-timer-arrow {
        margin-top: 20px;
    }
}
/* DESKTOP  */
@media (min-width: 1025px) {
    :root {
        --height-desktop: 1600px;
    }
    .paracontainer {
        top: 0;
        height: var(--height-desktop) !important;
    }
    .div-for-parallax {
        height: var(--height-desktop);
        margin-top: -205px; 
        /* 225 */
    }
    .mainparaimg {
        width: 100%;
        max-height: 1200px;
        position: absolute;
        z-index: 0;
    }
    .react-parallax {
        height: var(--height-desktop) !important;
        margin-top: -259px;
        /* 180 */
    }
    .style-flanc1 {
        width: 100%;
        position: absolute;
        margin-top: 398px;
        z-index: 2;
        transform: translateY(0px);
    }
    .style-flanc2 {
        width: 100%;
        position: absolute;
        margin-top: 574px;
        z-index: 1;
    }
    .text-oncarousel {
        margin-top: 700px !important;
        z-index: 10;
    }
    .para-second-title {
        margin-bottom: 120px;
    }
}
/* IPAD */
@media (max-width: 1024px) and (min-width: 600px) {
    :root {
        --height-ipad: 1100px;
    }
    .div-for-parallax {
        height: var(--height-ipad);
        justify-content: center;
        overflow: hidden;
        margin: auto !important;
        max-width: 100%;
        z-index: -1;
    }
    .mainparaimg {
        position: absolute;
        z-index: 0;
    }
    .mobile-screen-only {
        display: block;
    }
    .lg-screen-only {
        display: none;
    }
    .space-timer-arrow {
        margin-top: 350px;
    }
    .img-prop {
        height: var(--height-ipad);
        display: flex;
        z-index: -1;
        transform: translateX(-200px);
        margin-left: auto;
        margin-right: auto;
    }
    .text-oncarousel {
        font-size: 20px;
        margin-top: 270px !important;
        z-index: 3;
        color: white;
        text-align: center;
        width: 100%;
        position: absolute;
    }
    .space-header-timer {
        margin-top: 20px;
    }
    .space-timer-arrow {
        margin-top: 420px;
    }
}
/* MACBOOK SCREEN  */
@media (max-height: 780px) and (min-width: 1025px) {
    .paracontainer {
        top: 0;
        height: 1200px !important;
    }
    .div-for-parallax {
        height: 1200px;
        margin-top: -225px; 
        /* 225 */
    }
    .mainparaimg {
        width: 100%;
        height: auto;
        position: absolute;
        z-index: 0;
    }
    .react-parallax {
        height: 1000px !important;
        margin-top: -190px;
        /* 180 */
    }
    .style-flanc1 {
        width: 100%;
        position: absolute;
        margin-top: 398px;
        z-index: 2;
        transform: translateY(0px);
    }
    .style-flanc2 {
        width: 100%;
        position: absolute;
        margin-top: 514px;
        z-index: 1;
    }
    .text-oncarousel {
        margin-top: 600px !important;
        z-index: 10;
    }
}

